import React from "react";

import "./OnlineCustomers.css";

import pic1 from "./pic1.png";

import { useInView } from "react-intersection-observer";

export default function OnlineCustomers() {
  const [refBox1a, inViewBox1a] = useInView({
    triggerOnce: true,
    rootMargin: "0px 0px",
  });

  return (
    <div className="online-customer-main-container">
      <div className="online-customer-main-container-box-1">
        <div
          className={`online-customer-main-container-box-1a ${
            inViewBox1a ? "online-customer-slide-in-animation" : ""
          }`}
        >
          <p ref={refBox1a}>
            A custom-designed website can help online customers comprehend your
            brand's offerings and distinctive value propositions easily. They
            will be able to grasp the benefits of the products and services you
            offer, as well as appreciate their uniqueness in comparison with
            other competitors.
          </p>

          {/* <button>LET’S CHAT</button> */}
        </div>
      </div>

      <div className="online-customer-main-container-box-2">
        <img src={pic1} alt="pic1" />
      </div>
    </div>
  );
}
