import React, { useEffect } from "react";

import Navbar from "../../../components/genral_components/Navbar/Navbar";

import CustomWebsiteDesignBanner from "../../../components/custom_website_design_components/custom-website-design-components-banner/CustomWebsiteDesignBanner";

import FoundationCompany from "../../../components/custom_website_design_components/foundation-of-company/FoundationCompany";

import OnlineCustomers from "../../../components/custom_website_design_components/online-customers-understand/OnlineCustomers";

import SolutionsCustomWeb from "../../../components/custom_website_design_components/solutions-custom-web-design/SolutionsCustomWeb";

import MarketingStrategy from "../../../components/custom_website_design_components/marketing-strategy/MarketingStrategy";

import Pricing from "../../../components/services_page_components/pricing/Pricing";

import Footer from "../../../components/genral_components/Footer/Footer";

import OurSkills from '../../../components/home_page_components/ourskills/Skills'

import OurWork from '../../../components/home_page_components/ourwork/Ourwork'

import { useLocation } from "react-router-dom";

export default function CustomWebsiteDesign() {

  const location = useLocation();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [location]);
  
  return (
    <div className="custom-website-design-main-container">
      <Navbar />

      <CustomWebsiteDesignBanner />

      <FoundationCompany />

      <OnlineCustomers />

      <SolutionsCustomWeb />

      <MarketingStrategy />

      <br />
      <br />
      <br />
      <br />

      <Pricing />

      <br />
      <br />
      <br />

      <OurSkills/>

      <br />
      <br />
      <br />

      <OurWork/>

      <Footer />
    </div>
  );
}
