import React from "react";

import "./FoundationCompany.css";

import pic1 from "./pic1.png";

import { useInView } from "react-intersection-observer";

export default function FoundationCompany() {
  
  const [refBox1a, inViewBox1a] = useInView({
    triggerOnce: true,
    rootMargin: "0px 0px",
  });

  return (
    <div className="foundation-company-main-container">
      <div className="foundation-company-main-container-box-1">
        <div
          className={`foundation-company-main-container-box-1a ${
            inViewBox1a ? "slide-in-animation" : ""
          }`}
        >
          <p ref={refBox1a}>
            The foundation of any company is its custom website design
            businesses must have a bespoke website to create their brand image.
            A well-designed and tailored website serves as the base for any
            business.
          </p>

          <button>LET’S CHAT</button>
        </div>
      </div>

      <div className="foundation-company-main-container-box-2">
        <img src={pic1} alt="pic1"/>
      </div>
    </div>
  );
}
