import React from "react";

import "./SolutionsCustomWeb.css";

import pic1 from "./pic1.png";

import { useInView } from "react-intersection-observer";

export default function SolutionsCustomWeb() {
  const [refBox1a, inViewBox1a] = useInView({
    triggerOnce: true,
    rootMargin: "0px 0px",
  });

  return (
    <div className="solutions-custom-web-design">
      <br />
      <br />
      <br />

      <h1>What do custom web designers do?</h1>

      <br />

      <p>
        Our custom website design firm assesses the conversion prospects for
        your website through extensive studies. We evaluate these findings to
        ensure optimal outcomes.
      </p>

      <br />
      <br />

      <div className="solutions-custom-web-design-main-container">
        <div className="solutions-custom-web-design-main-container-box-1">
          <div
            className={`solutions-custom-web-design-main-container-box-1a ${
              inViewBox1a ? "solutions-custom-web-design-slide-in-animation" : ""
            }`}
          >
            <p ref={refBox1a}>
              Take control of your brand identification and multiple elements
              with our custom website designers and custom e-commerce website
              design. The custom design website will help convert visitors into
              customers and engage them in the sales process.
            </p>

            {/* <button>LET’S CHAT</button> */}
          </div>
        </div>

        <div className="solutions-custom-web-design-main-container-box-2">
          <img src={pic1} alt="pic1" />
        </div>
      </div>
    </div>
  );
}
