import React from "react";
import "./CustomWebsiteDesignBanner.css";

import { useNavigate } from "react-router-dom";

export default function CustomWebsiteDesignBanner() {
  const navigate = useNavigate();

  return (
    <div className="custom-website-design-banner">
      <h1>How can custom website design grow your business?</h1>

      <p>By increasing your website rankings and driving more traffic.</p>

      <div className="custom-website-design-banner-buttons">
        <button
          onClick={() => {
            navigate("/contactus");
          }}
        >
          LEARN MORE
        </button>

        <button
          onClick={() => {
            navigate("/portfolio");
          }}
        >
          OUR WORK
        </button>
      </div>
    </div>
  );
}
