import React from "react";

import "./MarketingStrategy.css";

import pic1 from "./pic1.png";

import { useInView } from "react-intersection-observer";

export default function MarketingStrategy() {
  const [refBox1a, inViewBox1a] = useInView({
    triggerOnce: true,
    rootMargin: "0px 0px",
  });

  return (
    <div className="marketing-strategy-main-container">
      <div className="marketing-strategy-main-container-box-1">
        <div
          className={`marketing-strategy-main-container-box-1a ${
            inViewBox1a ? "marketing-strategy-slide-in-animation" : ""
          }`}
        >
          <p ref={refBox1a}>
            As a renowned, custom design company, we focus on identifying your
            goals, strategizing your approach, and developing a distinctive
            e-Commerce website that aligns with your marketing strategy. With
            our expertise in implementing tailored web design services, we have
            successfully assisted numerous clients in achieving their
            objectives.
          </p>

          <button>LET’S CHAT</button>
        </div>
      </div>

      <div className="marketing-strategy-main-container-box-2">
        <img src={pic1} alt="pic1" />
      </div>
    </div>
  );
}
